import * as React from "react"
import { Link, useStaticQuery, graphql } from 'gatsby'
import { GlobalStyled, CustomMDXRender, MainDiv } from "../components"

let pageStyles = {}
// markup


const ql = graphql`
query SiteQueryAndArticleQuery {

  allMdx {
    nodes {
      id
      slug
      rawBody
      body
      excerpt
      frontmatter {
        date(formatString: "YYYY-MM-DD kk:mm")
        title
        draft
      }
    }
  }

  site {
    siteMetadata {
      siteUrl
      title
      description
    }
  }

}
`


const IndexPage = () => {
  const query = useStaticQuery(ql)

  return (
    <>
      <GlobalStyled />
      <main style={pageStyles}>
        <title>{query.site.siteMetadata.title}</title>
        <MainDiv>
          <h1>{query.site.siteMetadata.title}</h1>
          <h2>{query.site.siteMetadata.description}</h2>
          <br></br>
          {
            query.allMdx.nodes.map((val) => {
              return (
                <div key={val.slug}>
                  <h2><Link to={val.slug}>{val.frontmatter.title}{val.frontmatter.draft ? "（草稿）" : ""}</Link></h2>
                  <h3>{val.frontmatter.date}</h3>
                  <p>{val.excerpt}</p>
                  <br></br>
                </div>
              )
            })
          }
        </MainDiv>
      </main>
    </>
  )
}

export default IndexPage
